@import '../../App.scss';

.reservation {
    // padding-top: 92px;

    // @include breakpoints(sm) {
    //     padding-top: 80px
    // }

    .reservation-container {
        background-color: #f4f4f4;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        color: #fff;
        max-width: 100vw;
        min-height: calc(100vh - 235px);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        align-self: center;
        text-align: center;
        width: 100%;
        height: 100%;
        padding: 32px;

        @include breakpoints(md) {
            flex-direction: column;
        }

        @include breakpoints(sm) {
            flex-direction: column;
            min-height: calc(100vh - 212px);
        }
    }

    .reservation-image-container {
        height: 400px;
        width: 400px;
        margin-top: -100px;

        @include breakpoints(md) {
            height: 300px;
            width: 300px;
            margin-right: 0;
            margin-bottom: 5px;
        }

        @include breakpoints(sm) {
            height: 200px;
            width: 200px;
            margin-right: 0;
            margin-bottom: 3px;
        }
    }

    .reservation-image {
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url('../../../public/images/reserva.png');
    }

    .reservation-text-container {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        h1 {
            color: var(--color-3);
            font-size: 32px;
            @include breakpoints(sm) {
                font-size: 25px;
            }
        }

        h3 {
            color: var(--color-3);
            // font-weight: 400;
            margin-bottom: 32px;
            margin-top: 10px;
            font-size: 18px;

            @include breakpoints(sm) {
                font-size: 16px;
            }
        }

        button {
            background-color: transparent;
            border: none;
            font-family: 'Roboto';
            color: var(--color-principal);
            font-weight: 600;
            font-size: 20px;

            @include breakpoints(sm) {
                font-size: 16px;
            }
        }

    }

    .bottom-text-reservation {
        width: 60%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        align-self: center;
        text-align: center;

        @include breakpoints(sm) {
            width: 88%;
        }
    }
}